import React, { useState,useEffect } from 'react';
import IPage3 from './IPage3';

export default function IPage2() {
    const [isVisible, setIsVisible] = useState(true);
    const handelNextBtn=()=>{
      setIsVisible(false);
    }
    var randomNumber = Math.floor(Math.random() * ((50 / 2) - (10/ 2) + 1)) + (10 / 2);

    useEffect(() => {
    
    localStorage.setItem('magicNumber', randomNumber*2);
    console.log(randomNumber*2);

    }, []); // Empty array means this effect runs once after the initial render



    
    if(isVisible)
        {
      return (
        <div>
          <div className="alert alert-success mx-4 my-4 border-dark mb-3 border" role="alert" style={{"max-width": "50rem"}}>
            
      <h4 className="alert-heading roboto-mono2"> Add :{randomNumber*2} </h4>
      <p className='text2'>
      Now, add {randomNumber*2} to the result you got from doubling your guess. This will modify the number further.    
      </p>
      
      <iframe
              src="https://lottie.host/embed/4c5cbd24-6e9c-4148-916d-c252a1aec141/dABBMwx2dG.json"
              className='no-border mx-4'
              width="250" height="250"
              
            ></iframe>
      <hr />
      <div id="accordion">
  <div class="card">
    <div class="card-header" id="headingTwo">
      <h5 class="mb-0">
        <button class="btn btn-link collapsed" data-toggle="collapse" data-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo">
        Hint
        </button>
      </h5>
    </div>
    <div id="collapseTwo" class="collapse" aria-labelledby="headingTwo" data-parent="#accordion">
      <div class="card-body roboto-mono3">
      Add {randomNumber*2} to the result: 40 + {randomNumber*2} = {40+(randomNumber*2)}.
      </div>
    </div>
  </div>


  </div>

      
    </div>
    <div className='d-flex flex-column justify-content-center container' style={{"marginBottom":"15px", "max-width": "41rem"}}>
    <button type="button" className="btn btn-primary" onClick={handelNextBtn}> Next</button>
    </div>
        </div>
      )
    }
    else{
        return(<IPage3/>)
    }
}
