
import React, { useState, useEffect } from 'react';
import { ThreeCircles } from 'react-loader-spinner';
import toast, { Toaster } from 'react-hot-toast';
import Footer from './footer';
export default function IPage4() {
    const [isVisible, setIsVisible] = useState(false);
    const [isVisiblepinner, setSpinnerVisible] = useState(true);
    console.log(localStorage.getItem('magicNumber'))
    const handelNextBtn=()=>{
        window.location.reload();
    }
   

    // setTimeout(()=>{
    //     setIsVisible(true);
    //     setSpinnerVisible(false);
    //     toast.success('Successfully created!');
    // },3000);
    useEffect(() => {
        const timer = setTimeout(() => {
            setIsVisible(true);
            setSpinnerVisible(false);
            toast.success('Yeah, I Guessed it right! ');
        }, 3000);

        // Cleanup the timeout if the component unmounts
        return () => clearTimeout(timer);
    }, []); // Empty dependency array ensures this runs only once



   


    
    


    if(isVisible)
        {
      return (
        <div>
        <div className='border'>
            <Toaster />
        <div className="alert alert-success mx-4 my-4 border-dark mb-3" role="alert" style={{"max-width": "50rem"}}>
        <iframe
              src="https://lottie.host/embed/b547691b-7e30-4a86-8505-6c74d8b5df75/iscLjrkIXX.json"
              className='no-border mx-5'
              width="150" height="150"
              
            ></iframe>
      <h4 className="alert-heading final-ans" style={{"marginLeft":"40px"}}> The answer is .....</h4>
      <div className='mx-4'>      
      <h2
      style={{ "width":"150", "height":"150","marginLeft":"80px" }}
      className='final-ans'
      >
       {localStorage.getItem('magicNumber')/2}
      </h2>
      </div>
      
     
     
      
    </div>
    <div className='d-flex flex-column justify-content-center container' style={{"marginBottom":"15px", "max-width": "41rem"}}>
    <button type="button" className="btn btn-primary" onClick={handelNextBtn}>Try Agian </button>
    </div>
        </div>

        <Footer></Footer>
    
        </div>
        
      )
    }
    else{
        return(<div className='my-4 mx-3'> 

<ThreeCircles
  visible={isVisiblepinner}
  height="120"
  width="120"
  color="white"
  ariaLabel="three-circles-loading"
  wrapperStyle={{"marginLeft":"25px"}}
  wrapperClass=""
  />
  <div
  style={{
    "color": "white",
    marginTop: "40px"


  }}
  >
 <p className='letsPlayFont'>Let me guess the answer....</p>
 </div>
            
 </div>);

       

    }
}
