import React, { useState,useEffect } from 'react';
import GameComponent from './GameCompo';
import toast, { Toaster } from 'react-hot-toast';
import sound from '../assets/bg-music-two.mp3'
import './mainCss.css'
export default function MainPage() {
const [isVisible, setIsVisible] = useState(true);
    const handelPlayBtn=()=>{
    setIsVisible(false); 

    new Audio(sound).play();
    }

      
   



    
  
  return (
    <div>
      <Toaster />
        
      <div className="d-flex flex-column justify-content-center w-100 h-100">
     <div className="d-flex flex-column justify-content-center align-items-center">
     <iframe
          src="https://lottie.host/embed/e0d9e479-a90e-45d0-b13d-53394a693c7d/MYaRg2Di7T.json"
          className='no-border'
        ></iframe>
     
    <h1 className="fw-light text-white m-0 my-3 myFont">
    Number Wizard 
    </h1>
     {isVisible && (
    <div className="d-flex flex-column justify-content-center align-items-center">
    <button type="button" className="btn btn-outline-primary my-3"><h1 className='letsPlayFont' onClick={handelPlayBtn}>Let's Play</h1></button>

     
    <iframe
          src="https://lottie.host/embed/ca21ca30-8537-423d-9abf-effa4c46983e/1gFReGrABL.json"
          className='no-border '
          width="500" height="300"

        ></iframe>
    
   
  </div>
  )}
  {!isVisible && ( <GameComponent/>)}
 
   </div>
</div>
</div>
  )
}
