import React, { useState,useEffect } from 'react';
import IPage1 from './IPage1';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css' // Import css

export default function GameCompo() {
  const [isVisible, setIsVisible] = useState(true);
  const handelNextBtn=()=>{
    setIsVisible(false);
  }
  const options = {
    title: 'Welcome Ji ',
    message: "Welcome to the Number Guessing Game! Follow the instructions step by step, and I will guess the final number !",
    buttons: [
      {
        label: 'Got it!',
        
      },
    ],
    closeOnEscape: true,
    closeOnClickOutside: true,
    keyCodeForClose: [8, 32],
    willUnmount: () => {},
    afterClose: () => {},
    onClickOutside: () => {},
    onKeypress: () => {},
    onKeypressEscape: () => {},
    overlayClassName: "overlay-custom-class-name"
  };
 
  useEffect(() => {
    confirmAlert(options);
}, []); // Empty dependency array ensures this runs only once

  
  
  if(isVisible)
    {
  return (
    <div className='border'>
      <div className="alert alert-success mx-4 my-4 border-dark mb-3" role="alert" style={{"max-width": "50rem"}}>
  <h4 className="alert-heading roboto-mono"> Pick a random number </h4>
  
  <iframe
          src="https://lottie.host/embed/77637153-3b7b-4ef1-acde-8abdd74d1885/dl1OqimW9Y.json"
          className='no-border mx-4'
          width="250" height="250"
          
        ></iframe>
  <hr />
  
  
</div>
<div className='d-flex flex-column justify-content-center container' style={{"marginBottom":"15px", "max-width": "41rem"}}>
<button type="button" className="btn btn-primary" onClick={handelNextBtn}> Next</button>
</div>
    </div>
  )
}
else{
  return(<IPage1></IPage1>)
}

}
